.Homepage {
    background-color: #EDEDED;
}

.Homepage header {
    padding: 2rem 5rem;
    width: 60%;
    margin: 0 auto;
    transition: all .4s ease-in-out;
}

@media screen and (max-width: 1530px) {
    .Homepage header {
        width: 95%;
    }
}


body {
    font-family: 'Arial', sans-serif;
    line-height: 1.6;
    margin: 20px;
    color: #333;
    background-color: #f4f4f4;
  }
  

header {
    background-color: #4CCD99;
    color: #ffffff;
    padding: 20px 0;
    text-align: center;
  }
  
  .search-container {
    margin-bottom: 20px;
  }
  
  .search-form input[type="text"] {
    padding: 10px;
    width: 70%;
    border: none;
    margin-right: 5px;
  }
  
  .search-form button {
    padding: 10px 15px;
    background-color: #ff9900;
    color: white;
    border: none;
    cursor: pointer;
  }
  
  .search-form button:hover {
    opacity: 0.9;
  }
  
  .button-container {
    display: flex;
    justify-content: center;
    gap: 10px;
    margin-top: 20px;
  }
  
  .filter-btn {
    background-color: #007F73;
    color: #ffffff;
    border: none;
    padding: 10px 20px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .filter-btn:hover {
    background-color: #007F73;
  }
  
  @media only screen and (max-width: 768px) {
    .search-form input[type="text"] {
      width: 60%;
    }
  
    .filter-btn {
      padding: 8px 16px;
    }
  }

.AnimeItem {
    padding: 3rem 18rem;
    background-color: #EDEDED;
}

  
.AnimeItem h1 {
    font-size: 24px;
    margin-bottom: 20px;
    text-align: center;
}
.AnimeItem .title {
    font-size: 24px;
    /* margin-bottom: 20px; */
    margin: 2rem auto;
    text-align: center;
}

.AnimeItem h1:hover {
    transform: skew(-3deg);
}

.AnimeItem .description {
    margin-top: 2rem;
    color: #6c7983;
    line-height: 1.7rem;
}



.AnimeItem .trailer-con {
    display: flex;
    justify-content: center;
    align-items: center;
}

.AnimeItem .trailer-con iframe {
    outline: none;
    border: 5px solid #e5e7eb;
    aspect-ratio: 16 / 9;
    padding: 1.5rem;
    border-radius: 10px;
    width: 100%;
    background-color: #FFFFFF;
}

.AnimeItem .details {
    background-color: #fff;
    border-radius: 20px;
    padding: 2rem;
    border: 5px solid #e5e7eb;
}


.AnimeItem .details .detail {
    display: flex;
    flex-direction: column; 
  }

.AnimeItem .image img {
    width: 80%;
    max-width: 200px;
    margin-bottom: 20px;
}
  
  .AnimeItem .anime-details {
    text-align: center; 
  }
  
  
.AnimeItem {
  padding: 1rem;
  font-family: Arial, sans-serif; 
}

.AnimeItem .button {
  background-color: #007F73;
  border: 1px solid #ddd; 
  padding: 10px 20px;
  margin-bottom: 1rem;
  cursor: pointer;
  font-size: 1rem;
}

.AnimeItem .button:hover {
  background-color: #007F73;
}

.AnimeItem h1 {
  color: #333;
  text-align: center;
  margin-bottom: 1rem;
}

.AnimeItem .image {
  text-align: center; 
  margin-bottom: 1rem;
}

.AnimeItem .image img {
  width: 100%;
  max-width: 400px;
  height: auto;
  margin: 0 auto;
}

.AnimeItem .details {
  text-align: left;
}

.AnimeItem .anime-details p {
  font-size: 0.9rem; 
  margin-bottom: 0.5rem;
}

.AnimeItem .anime-details p span {
  font-weight: bold;
}

.AnimeItem .description {
  margin-bottom: 1rem;
  line-height: 1.5; 
}

.AnimeItem .description button {
  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer;
  font-size: 1.2rem;
  color: #27AE60;
  font-weight: 600;
}

.AnimeItem .description button:hover {
  opacity: 0.9;
}

.AnimeItem .trailer-con {
  margin-bottom: 1rem;
}

.AnimeItem .iframe {
  width: 100%;
  height: auto;
  background: #000;
}

@media (max-width: 768px) {
  .AnimeItem .image img {
    max-width: 100%;
  }
}
